 /**
 * Imports external styles.
 * We compile it as a less file in order to wrap the external CSS rules.
 */
@import (less) "node_modules/powerbi-visuals-utils-interactivityutils/lib/index.css";
@import (less) "node_modules/powerbi-visuals-utils-formattingutils/lib/index.css";
@import (less) "node_modules/powerbi-visuals-utils-chartutils/lib/index.css";

.timeline-component {
    cursor: default;

    .timeline {
        font-family: helvetica, arial, sans-serif;

        -webkit-tap-highlight-color: transparent;

        cursor: default;

        overflow-y: hidden;

        .label,
        .mainArea .label,
        .upperTextCell,
        .lowerTextCell {
            text-anchor: middle;
        }

        .mainArea .above{
            transform: translate(0, 0);
        } 

        .mainArea .inside{
            transform: translate(0, 25%);
        }

        .mainArea .below{
            transform: translate(0, 50%);
        }

        .cellRect {
            stroke: #333444;
        }

        .selection {
            &Cursor {
                fill: gray;
            }

            &RangeContainer {
                text-anchor: start;
            }
        }

        .periodSlicerSelectionRect,
        .timelineVertLine,
        .cellRect {
            cursor: pointer;
        }

        .selectionCursor {
            cursor: ew-resize;
        }
    }

    .timelineSlicer {
        fill: #000000;

        cursor: pointer;

        .periodSlicer {
            &Rect {
                stroke: #aaa;
                stroke-width: 2px;
                fill: transparent;
            }

            &Granularities,
            &Selection {
                font-size: 10px;
                text-anchor: middle;
            }

            &SelectionRect {
                fill: transparent;
            }
        }

        .periodSlicerRect {
            pointer-events: none;
        }
    }

    .rangeTextArea.left{
        transform: translate(0, 0);
    }

    .rangeTextArea.right{
        transform: translate(88%, 0);
    }

    .rangeTextArea.center{
        transform: translate(44%, 0);
    }

    .timelineWrapper {
        overflow: auto;
        width: 100%;
    }
}
